.nav__list {
  margin: 0;
  display: flex;
  align-items: center;
}

.nav__item {
  margin-right: 35px;
}

.nav__link {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  font-family: 'Muli-Regular', sans-serif;
  color: rgb(255, 255, 255);
  line-height: 2.7;
  letter-spacing: 0.3px;
  text-align: center;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
}


.nav__link:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
  color: brown;
}
