.footer {
  width: 100%;
  margin-top: 15px;
  display: flex;
  flex-direction: column;
}

.footer__top {
  min-height: 210px;
  padding: 60px 80px 85px 97px;
  display: flex;
  background-color: #ff9933;
  justify-content: space-between;
  align-items: baseline;
  flex-wrap: wrap;
}

.footer__logo {
  display: flex;
  align-self: center;
  // flex-grow: 1;
}

.footer__title {
  margin-top: 10px;
  font-size: 26px;
  font-family: 'Muli-ExtraBold', sans-serif;
  color: rgb(255, 255, 255);
  line-height: 0.9;
  text-align: left;
  align-items: center;
}
.footer__subs {
  display: flex;
  flex-direction: column;
  // flex-grow: 1;
  // max-width: 495px;
}

.footer__input {
  width: 430px;
  height: 25px;
  border-radius: 35px;
  -webkit-border-radius: 35px;
  -moz-border-radius: 35px;
  -ms-border-radius: 35px;
  -o-border-radius: 35px;
}
.footer__arrow {
  width: 50px;
  height: 50px;
  margin-right: 15px;
}

.footer__follow-us {
  display: flex;
  justify-content: center;
}

.footer__follow {
  font-size: 18px;
  font-family: "Muli-ExtraBold";
  color: rgb(109, 69, 12);
  font-weight: bold;
  text-transform: uppercase;
  line-height: 2.222;
  text-align: left;
  margin-right: 40px;
}

.footer__social {
  display: flex;
  justify-content: flex-end;
}
.footer__bottom {
  position: relative;
  height: 60px;
  background-color: #663333;
  display: flex;
  justify-content: center;
}

.footer__small {
  color: #999999;
  font-size: 13px;
  font-family: "Muli Regular";
  text-align: center;
  align-self: center;
}

.footer__buton {
  min-width: 120px;
  min-height: 80px;
  position: fixed;
  right: 50px;
  bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.footer__link {
  color: #fff;
  font-size: 16px;
  font-family: "Muli-Bold";
  line-height: 2.5;
  text-align: left;
  // position: absolute;
  // top:10px;
  // right: 130px;
}

.footer__arrow-top {
  background-color: #ff6633;
  color: #fff;
  width: 51px;
  height: 51px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: flex-start;
  border-radius: 10%;
  //  position: absolute;
  //  bottom:35px;
  //  right:50px;
}
