/* header block */
.header {
  height: 1080px;
  background: url("./../images/header-bg-img.jpg") no-repeat;
  background-size: cover;
  background-position: center;
  color: #fff;
  display: flex;
  flex-direction: column; }

.header__top {
  padding: 9rem;
  position: relative;
  transition: 0.8s; }

.header__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__burger-button {
  width: 50px;
  position: fixed;
  top: 50px;
  right: 50px;
  display: none;
  z-index: 9999; }

.header__burger-button-img {
  width: 100%; }

.header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  -webkit-transform: translate(-50%, -60%);
  -moz-transform: translate(-50%, -60%);
  -ms-transform: translate(-50%, -60%);
  -o-transform: translate(-50%, -60%); }

.header__rigth-block {
  display: flex;
  align-items: center;
  position: relative; }

.header__personal {
  display: flex;
  justify-content: center;
  align-items: center; }

.header__personal::after {
  content: "";
  display: inline-block;
  background: url("./../images/header-line.png");
  background-size: cover;
  position: absolute;
  top: 10px;
  left: 48%; }

.header__social {
  margin-left: 4px; }

.header__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px; }

.nav__list {
  margin: 0;
  display: flex;
  align-items: center; }

.nav__item {
  margin-right: 35px; }

.nav__link {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 16px;
  font-family: 'Muli-Regular', sans-serif;
  color: white;
  line-height: 2.7;
  letter-spacing: 0.3px;
  text-align: center;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s; }

.nav__link:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
  color: brown; }

.personal__list {
  display: flex;
  margin-right: 132px;
  padding-left: 5px; }

.personal__item {
  margin-right: 45px; }

.personal__item:last-child {
  margin-right: 0px; }

.personal__counter {
  position: absolute;
  top: 5px; }

.personal__counter {
  background-color: #fff;
  display: inline-block;
  height: 15px;
  width: 15px;
  text-align: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  color: black;
  font-size: 10px;
  text-transform: uppercase; }

.social__list {
  width: 130px;
  height: 40px;
  display: flex;
  justify-content: space-around;
  align-items: flex-start; }

.social__item {
  position: relative;
  margin-right: 10px;
  background-color: #fff;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%; }

.social__link {
  background-color: #fff;
  display: inline-block;
  height: 33px;
  width: 33px;
  font-size: 10px;
  text-align: center;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%; }

.social__item:last-child {
  margin-right: 0px; }

.social__img {
  padding-top: 9px; }

.social__item-footer {
  background-color: #ff9933;
  padding-top: 15px; }

.social__item-footer a {
  background-color: #ff9933; }

.button {
  font-size: 2.4rem;
  font-family: 'Muli-ExtraBold', sans-serif;
  color: white;
  padding: 0 3.5rem;
  width: 280px;
  height: 70px;
  line-height: 1.667;
  text-align: left;
  border-radius: 3.5rem;
  -webkit-border-radius: 3.5rem;
  -moz-border-radius: 3.5rem;
  -ms-border-radius: 3.5rem;
  -o-border-radius: 3.5rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s; }

.button--green {
  background-color: #0aa566;
  margin: auto; }

.button--green:hover {
  background-color: #4db48b;
  color: white; }

.button--green::after {
  content: " ";
  width: 30px;
  height: 15px;
  display: inline-block;
  background-image: url("./../images/arrows-rigth.png");
  background-size: cover;
  position: absolute;
  right: 3.5rem;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s; }

.button--green:hover::after {
  transform: translateX(1rem);
  -webkit-transform: translateX(1rem);
  -moz-transform: translateX(1rem);
  -ms-transform: translateX(1rem);
  -o-transform: translateX(1rem); }

.button--yellow {
  background-color: #f5ae3e; }

.button--orange {
  background-color: #c3491e; }

.button--brown {
  background-color: #602e15; }

.button--orange:hover {
  background-color: #c56f50; }

.button--yellow:hover {
  background-color: #f5c87f; }

.button--brown:hover {
  background-color: #7e4d26; }

/* block smoothies */
.smoothies {
  display: flex;
  justify-content: center; }

